import { useState, useEffect, useRef } from 'react';
import * as mobilenet from "@tensorflow-models/mobilenet";



const questionList = [
  {description: ' Uses React.js for the standalone front-end application.', key: 0},
  {description: ' Gets the uploaded image for Image prediction', key: 1},
  {description: ' Achieve detection using TensorFlow.js', key: 2}
];

 

function App() {
  const [isModelLoading, setIsModelLoading] = useState(false)
  const [model, setModel] = useState(null)
  const [imageURL, setImageURL] = useState(null);
  const [results, setResults] = useState([])
  const [history, setHistory] = useState([])

  const imageRef = useRef()
  const textInputRef = useRef()
  const fileInputRef = useRef()

  const loadModel = async () => {
      setIsModelLoading(true)
      try {
          const model = await mobilenet.load()
          setModel(model)
          setIsModelLoading(false)
      } catch (error) {
          console.log(error)
          setIsModelLoading(false)
      }
  }

  const uploadImage = (e) => {
      const { files } = e.target
      if (files.length > 0) {
          const url = URL.createObjectURL(files[0])
          setImageURL(url)
      } else {
          setImageURL(null)
      }
  }

  const identify = async () => {
      textInputRef.current.value = ''
      const results = await model.classify(imageRef.current)
      setResults(results)
  }

  const handleOnChange = (e) => {
      setImageURL(e.target.value)
      setResults([])
  }

  const triggerUpload = () => {
      fileInputRef.current.click()
  }

  useEffect(() => {
      loadModel()
  }, [])

  useEffect(() => {
      if (imageURL) {
          setHistory([imageURL, ...history])
      }
  }, [imageURL])

  if (isModelLoading) {
      return <h2>Model Loading...</h2>
  }

  
  
  
  return (
    

    <div className="App" >
      <div style={{backgroundImage: `url("https://img.freepik.com/free-photo/decorative-rough-background_24972-295.jpg?w=740&t=st=1675070384~exp=1675070984~hmac=82bb8f1fcf81c291ef691be75f750bb27d94a79fa840afcaf56b86663b3cda79")`,backgroundSize:"cover",paddingBottom:150}} >
      <div className='bg' >
            <h1 className='header' style={{textAlign:"center"}}>IMAGE PREDICTION</h1> 
        </div>
        <div style={{marginLeft:20,paddingTop:20}}>
          <div style={{textAlign:"center"}}>
            <h2 style={{marginLeft:0,color:"darkblue"}}>About the Project </h2>
            <p style={{fontSize:18}}>The project is the Image prediction Application done using Rectjs and Tensorflow js </p>
          
            <a href='#image'><button style={{color:"white",backgroundColor:"blueviolet",padding:10,borderRadius:5,
            fontSize:15,marginTop:20,marginRight:35}} 
            >Predict Image</button></a>
               
          </div>
          <br></br>
          <div style={{paddingTop:5,marginLeft:80}}>
            <h2 style={{marginLeft:0,color:"darkblue"}}>How does it work?</h2>
            <ol style={{paddingLeft:33,fontSize:18}}>
              {questionList.map(question => {
              return (
               <li key={question.key } style={{paddingTop:10}}>{question.description}</li>
              );
              })}
             </ol>
          </div>
          <div style={{flexDirection:"row",float:"right",marginTop:-180,marginRight:100}} >
            <h2 style={{marginLeft:0,color:"darkblue",paddingTop:15}}>Tensorflow.js</h2>
            <p style={{fontSize:18,paddingBottom:10}}>•   TensorFlow.js is a library for machine learning in JavaScript</p>
            <p style={{fontSize:18}}>•    TensorFlow.js provides many pre-trained models that simplify <br style={{paddingTop:10}}></br> the time-consuming task of training a machine learning model <br style={{paddingTop:10}}></br>from scratch</p>
          </div>
          <div>
            <h2 style={{marginLeft:80,color:"darkblue",paddingTop:35}}>Result</h2>
            <p style={{fontSize:18,paddingBottom:10,marginLeft:80}}>• TensorFlow.js is a library for machine learning in JavaScript</p>
            <p style={{fontSize:18,paddingBottom:10,marginLeft:80}}> • Gives the Best Guess among them , makes simpler for the user </p>
            <p style={{fontSize:18,paddingBottom:10,marginLeft:80}}>• A front-end-based powered TF model can be cost-efficient as most<br style={{paddingTop:10}}></br> hosting for front-end are cheaper compared
             to a server-powered  <br style={{paddingTop:10}}></br> TensorFlow solution , moreover, it can protect the privacy of users </p>
          </div>
        </div>
        </div >

    
    
    <div className='bg' style={{backgoundColor:"white"}} id="image">
    <h1 className='header'>IMAGE PREDICTION</h1> 
    <div className='inputHolder'>
        <input type='file' accept='image/*' capture='camera' className='uploadInput' onChange={uploadImage} ref={fileInputRef} />
        <button className='uploadImage' onClick={triggerUpload}>Upload Image</button>
        <span className='or'>OR</span>
        <input type="text" placeholder='Paster image URL' ref={textInputRef} onChange={handleOnChange} />
    </div>
    
    <div className="mainWrapper">
        <div className="mainContent">
            <div className="imageHolder">
                {imageURL && <img src={imageURL} alt="Upload Preview" crossOrigin="anonymous" ref={imageRef} />}
            </div>
            {results.length > 0 && <div className='resultsHolder'>
                <h1 className='pred'>Predictions</h1>
                {results.map((result, index) => {
                    return (
                        <div className='result' key={result.className}>
                            <span className='name'>{result.className}</span>
                            <span className='confidence'>Confidence level: {(result.probability * 100).toFixed(2)}% {index === 0 && <span className='bestGuess'>Best Guess</span>}</span>
                        </div>
                    )
                })}
            </div>}
        </div>
        {imageURL && <button className='button' onClick={identify}>Identify Image</button>}
    </div>
    {history.length > 0 && <div className="recentPredictions">
        <h2>RECENT IMAGES</h2>
        <div className="recentImages">
            {history.map((image, index) => {
                return (
                    <div className="recentPrediction" key={`${image}${index}`}>
                        <img src={image} alt='Recent Prediction' onClick={() => setImageURL(image)} />
                    </div>
                )
            })}
        </div>
    </div>}
    </div>
</div>

  );
}

export default App;
